<template>
  <div>
    <h1>II类道路检测</h1>
    <el-row>

    </el-row>
    <el-row>
      <div class="grid-content bg-purple-light">
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          stripe="true"
          style="width: 100%"
          max-height="540">
          <el-table-column
            align="center"
            label="道路名称"
            width="250">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top">
                <p>姓名: {{ scope.row.name }}</p>
                <p>住址: {{ scope.row.address }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label="道路状态"
            width="250">
            <p>正常</p>
          </el-table-column>
          <el-table-column
            align="center"
            label="最近巡查日期"
            width="180">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <span style="margin-left: 10px">{{ scope.row.date }}</span>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="check"
            label="是否需要检测"
            width="250"
            :filters="[{ text: '是', value: '是' }, { text: '否', value: '否' }]"
            :filter-method="filterTag"
            filter-placement="bottom-end">
            <p>否</p>
          </el-table-column>
          <el-table-column
            align="center">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder="输入关键字搜索"/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">巡查记录</el-button>
              <el-button
                size="mini"
                type="warning"
                @click="handleAdd(scope.$index, scope.row)">添加预警</el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">取消预警</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-row>

    <el-dialog title="设施损害通知单" :visible.sync="dialogTableVisible" width="70%" destroy-on-close="true" :destroyOnClose="true">
      <el-form ref="c1rform" :model="c1rform" label-width="80px" :rules="rulesc1rform">
        <el-row>
          <el-col :span="8"><div class="grid-content bg-purple">
            <el-form-item label="通知单号：" label-width="100px" prop="NoticeNo">
              <el-input v-model="c1rform.NoticeNo" :disabled="true"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="16"><div class="grid-content bg-purple-light">
            <el-form-item label="设施名称：" label-width="100px" prop="FacilityName">
              <el-input v-model="c1rform.FacilityName"></el-input>
            </el-form-item>
          </div></el-col>
        </el-row>
        <el-row>
          <el-col :span="8"><div class="grid-content bg-purple">
            <el-form-item label="整修时间：" label-width="100px" prop="RenovationTime">
              <el-date-picker type="date" placeholder="选择日期" v-model="c1rform.RenovationTime" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </div></el-col>
          <el-col :span="16"><div class="grid-content bg-purple-light">
            <el-form-item label="损坏部位：" label-width="100px" prop="DamageToParts">
              <el-input v-model="c1rform.DamageToParts"></el-input>
            </el-form-item>
          </div></el-col>
        </el-row>
        <el-form-item label="损坏原因：" label-width="100px">
          <el-input type="textarea" v-model="c1rdalyform.DamageReason"></el-input>
        </el-form-item>
        <el-form-item label="处理意见：" label-width="100px">
          <el-input type="textarea" v-model="c1rdalyform.ProcessingOpinion"></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-form-item label="技术员：" label-width="100px" align="center" prop="Technician">
              <el-input v-model="c1rform.Technician"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-form-item label="巡视员：" label-width="100px" align="center" prop="Inspector">
              <el-input v-model="c1rform.Inspector"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <el-form-item label="班长：" label-width="100px" align="center" prop="Monitor">
              <el-input v-model="c1rform.Monitor"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">
            <el-form-item label="时间：" label-width="100px" align="center" prop="Date">
              <el-date-picker type="date" placeholder="选择日期" v-model="c1rform.Date" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </div></el-col>
        </el-row>
        <el-form-item>
          <el-button type="primary" @click="onSubmitC1R('c1rform')">立即创建</el-button>
          <el-button @click="cl">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-drawer :visible.sync="drawer" :with-header="false" size="30%" :destroy-on-close="true">

      <div class="toptop">
        <el-form ref="c1rdalyform" :model="c1rdalyform" label-width="200px" :rules="rulesc1rdalyform">
          <el-form-item label="巡查人员：" label-width="110px" align="center" prop="Inspectors">
            <el-input v-model="c1rdalyform.Inspectors"></el-input>
          </el-form-item>
          <el-form-item label="损害类型：" label-width="110px" align="center" prop="DamageType">
            <el-input v-model="c1rdalyform.DamageType"></el-input>
          </el-form-item>
          <el-form-item label="位置及描述：" label-width="110px" prop="LocationAndDescription">
            <el-input type="textarea" v-model="c1rdalyform.LocationAndDescription"></el-input>
          </el-form-item>
          <el-form-item label="巡查日期：" label-width="110px" align="center" prop="InspectDate">
            <el-date-picker type="date" placeholder="选择日期" v-model="c1rdalyform.InspectDate" style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item label="备注：" label-width="110px">
            <el-input type="textarea" v-model="c1rdalyform.Note"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmitC2R('c1rdalyform')">立即创建</el-button>
            <el-button @click="dalycheck">取消</el-button>
          </el-form-item>
        </el-form>
      </div>

    </el-drawer>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        search: '',
        table_index: undefined,
        table_row: undefined,
        dialogTableVisible: false,
        drawer: false,
        c1rform: {
          Name: 'ClassIIRoad',
          RoadName: '',
          NoticeNo: '',
          FacilityName: '',
          DamageToParts: '',
          RenovationTime: '',
          DamageReason: '',
          ProcessingOpinion: '',
          Technician: '',
          Inspector: '',
          Monitor: '',
          Date: ''
        },
        c1rdalyform:{
          Name : 'ClassIIRoad',
          RoadName: '',
          Inspectors: '',
          InspectDate: '',
          DamageType: '',
          LocationAndDescription: '',
          Note: ''
        },
        rulesc1rform: {
          NoticeNo: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          FacilityName: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          RenovationTime: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ],
          DamageToParts: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          Technician: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          Inspector: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          Monitor: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          Date: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ]
        },
        rulesc1rdalyform: {
          Inspectors: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          InspectDate: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          DamageType: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          LocationAndDescription: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ],
          Note: [
            { required: true, message: '内容不能为空', trigger: 'blur' }
          ]
        }
      }
    },
    mounted:function () {
      this.loadTableData();
    },
    methods: {
      handleEdit(index, row) {
        var _this = this;
        _this.table_index = index;
        _this.table_row = row;
        var MyDate = new Date();
        _this.c1rdalyform.InspectDate = MyDate.toLocaleDateString();
        _this.drawer = true;
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleAdd(index, row){
        console.log(index, row);
        var _this = this;
        _this.table_index = index;
        _this.table_row = row;
        var MyDate = new Date();
        _this.c1rform.NoticeNo = (MyDate.getFullYear()%1000) + "" + (MyDate.getMonth()+1) + "" +  MyDate.getDate() + "" + MyDate.getHours() + "" + MyDate.getMinutes();
        _this.dialogTableVisible = true;
      },
      cl(){
        this.dialogTableVisible = false;
      },
      dalycheck(){
        this.drawer = false;
      },
      loadTableData(){
        var _this = this;
        this.getRequest("/rc/c1r").then(resp =>{
          if (resp && resp.status == 200){
            var data = resp.data;
            _this.tableData = data.tableData;
          }
        })
      },
      filterTag(value, row) {
        return row.check === value;
      },
      onSubmitC1R(form) {
        this.$refs[form].validate((valid) => {
          if (valid) {
            alert('submit!');
            this.$refs[form].resetFields();
            this.dialogTableVisible = false;
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      onSubmitC2R(form) {
        this.$refs[form].validate((valid) => {
          if (valid) {
            alert('submit!');
            this.$refs[form].resetFields();
            this.drawer = false;
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style>
  .toptop {
    position:fixed;
    top: 130px;
    margin-right: -40px;
  }
</style>



